import config from "../config/index";
import {
    makeAxiosGetRequest,
    makeAxiosPostRequest
} from "./http-service";


/**
 * createPost
 * @param {object} data
 */

export const getBlogCategories = (params) => {
    return new Promise((resolve, reject) => {
        makeAxiosGetRequest(`${config.baseUrl}get-blog-categories`, true, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                
                reject(e.response);
            });
    });
};

