import React, { useState, useEffect } from 'react';
import A from '../A';
import {GetAssetImage} from '../../globalFunctions';
import { switchLoader, alertBox} from '../../commonRedux/';
import Button from '../Button';
import { getWalletSession, getKYC, getKYCDetails, getAllBalance} from '../../http/wallet-calls';
import { walletAuth } from '../../http/http-calls';
import { getMonthlyBalance } from '../../http/trans-calls';
import walletCheck from '../../hooks/walletCheck';
import './styles.css';

const WalletBalanceWidget = props => {
  const [coins, setCoins] = useState([]);
  const [balance, setBalance] = useState(0);
  const [monthlyBlance, setMonthlyBalance] = useState(0);
  const [status, setStatus] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    // var kycStatus = window.localStorage.getItem("kycStatus");
    // if(kycStatus != null){
    //     kycStatus = kycStatus;
    //     setLoading(false);
    //     setStatus(kycStatus);
    //     allBalance();
    // }
    walletCheck().then(resp=>{
      window.localStorage.setItem("kycStatus", resp.data.status);
      setLoading(false);
      setStatus((resp.data.status == undefined) ? '' : resp.data.status);
      allBalance();
    }, err => {
      setStatus('');
      setLoading(false);
      setError("Authentication Error!");
    })
    
  }, []);

  const getMonthly = (co = coins) => {
    var date = new Date();
    var firstDay = new Date(date.getFullYear(), date.getMonth(), 1).toISOString(); 
    var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0).toISOString(); 
   
    getMonthlyBalance({start: firstDay, end: lastDay}).then(resp=>{
      console.log(resp);
      if(resp.length > 0){
        console.log(co);
        var balance = 0;
        resp.forEach(element => {
          balance += (element.amount*co[element.currency]);
        });
        setMonthlyBalance(balance);
      }
    }, err => {
      console.log(err);
    })
  }
  


  const allBalance = () =>{
      // var allBalance = window.localStorage.getItem("allBalance");
      // if(allBalance != null){
      //     allBalance = JSON.parse(allBalance);
      //     setCoins(allBalance);
      //     setError('');
      //     var balance = 0;
      //       allBalance.forEach(element => {
      //         balance += element.balance*element.EstimatedUSD;
      //       });
      //       setBalance(balance);
            
      //     setLoading(false);
      // }
      getAllBalance().then(async resp => {
          setCoins(resp.data);
          //console.log(coins);
          var balance = 0;
          var len = resp.data.length;
          var co = {};
          resp.data.forEach((element, i) => {
            balance += element.balance/element.EstimatedUSD;
            co[element.currencySymbol] = element.EstimatedUSD;
            if(i == (len-1)){
              setBalance(balance);
              getMonthly(co);
              setError('');
              setLoading(false);
            }
          });
          window.localStorage.setItem("allBalance", JSON.stringify(resp.data));
      }, error => {
          try {
              setStatus('');
              setLoading(false);
              setError(error.data.toString());
          } catch (error) {
              setStatus('');
              setLoading(false);
              setError("Error")
          }
      });
  }

  const toggleBody = () => {
    var body = document.getElementById("walletWidgetBody");
    if(body != null){
    if(body.classList.contains("open")){
      body.classList.remove("open");
    }else{
      body.classList.add("open");
    }
  }
  };


    return (
        <div className={"row empty-inner-container-with-border widget balanceWidget m-0 "+(props.variant != null && " "+props.variant)} >
          <div className="col">
                <div className="widgetTitle mb-0 pointer" onClick={toggleBody}>
                  <div> My Crypto Wallet</div>
                  {/* <i className="fa fa-chevron-down text-small" ></i> */}
                </div>
                {/* {
                  status == null ?
                  <div></div>
                  : 
                  status == 'Accept' ?*/}
                  <div className="widgetBody" id="walletWidgetBody">
                  <h4 className="full-show">Balance</h4>
                  <div className="balance">${balance.toFixed(6)}</div>
                  <h4 className="full-show">Earnings This Month</h4>
                  <div className="balance full-show">${monthlyBlance.toFixed(6)}</div>
                  <div className="flex-row-between full-hide">
                  <A href="/wallet/transfer"><Button variant="dark" size="small" className="w-48">Send</Button></A>
                  <A href="/wallet/deposit"><Button variant="dark" size="small" className="w-48">Receive</Button></A>
                  </div>
                  {/* <div className="text-right full-show">
                    <A href="/wallet" className="see-more">Show More</A>
                  </div> */}
                </div>
                {/* :
                <div className="widgetBody open" id="walletWidgetBody">
                  <p>Your KYC verification is not yet done.</p>
                  <A href="/wallet/verification"><Button variant="dark" size="small" className="w-98">Verify Now</Button></A>
                </div>
                } */}
                
          </div>
        </div>
    )
}

export default WalletBalanceWidget;