import { Link } from 'react-router-dom';
import React from 'react';
import { GetAssetImage, profilePic } from '../../globalFunctions';
import { getSuggestions, followUser, getPendingRequests, acceptRequest,cancelRequest } from '../../http/http-calls';
import ContentLoader from "react-content-loader";
import Button from '../Button';
import { switchLoader, alertBox } from '../../commonRedux/';
import { friendRequest, connectSocket } from '../../hooks/socket';
import A from '../A';
import './style.css';

class RecommendedFriends extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            search:false,
            users: [],
            seemore:false,
            toggleSkeleton: false
        };
    } 
    getSuggestedFriends = (key='') => {
        console.log(key);
        getSuggestions({ limit: 6, page: 1, key: key }, true)
        .then(async resp => {
            console.log("respresp")
            console.log(resp.message);
            this.setState({ users: resp.message, toggleSkeleton: true }, () => {
                this.checkMore(1, key);
            });
        }, error => {
            
        });
    }

    checkMore = (i, key = '') => {
        getSuggestions({ limit: 6, page: i+1, key:key }, true)
        .then(async resp => {
           if(resp.message.length > 0){
               this.setState({
                   seemore: true
               })
           }else{
            this.setState({
                seemore: false
            })
           }
        }, error => {
            
        });
    }


  search = (e) => {
    var val = e.target.value;
    if(val.length > 1){
      this.getSuggestedFriends(val);
    }else if(val == ''){
      this.getSuggestedFriends();
    }
  };


    componentDidMount() {
        this.getSuggestedFriends();
        if(this.props.currentUser != undefined){
            connectSocket(this.props.currentUser._id);
        }
        friendRequest((request) => {
            console.log(request);
            this.getSuggestedFriends();
        });
    }

    addFriend = (id, userIndex) => {
        switchLoader(true, 'Following...! ');
        followUser({ followerid: id }, true)
          .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            tempUser[userIndex]._id = resp._id;
            tempUser[userIndex].request = resp.request;
            tempUser[userIndex].type = "sent";
            tempUser[userIndex].uid = resp.followerid;
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
            
          });
      }

    //   callPendingRequests = () => {
    //     getPendingRequests({}, true)
    //       .then(async resp => {
    //         resp.length == 0 && setShowSkeletonPR(false) ;
    //       }, error => {
    //         switchLoader();
            
    //       });
    //   }
    
    //   callSuggestions = () => {
    //     getSuggestions({ limit: 10, page: 1 }, true)
    //       .then(async resp => {
    //         resp.message.length == 0 && setShowSkeleton(false);
    //         switchLoader();
    //       }, error => {
    //         switchLoader();
            
    //       });
    //   }
    

    
      accept = (user, index, item) => {
        switchLoader(true, 'Following...! ');
        if(item == 0){
            acceptRequest({ id: user._id }, true)
            .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            delete tempUser[index];
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
          });
        }else{
            cancelRequest({id: user.uid}, true)
            .then(async resp => {
              switchLoader();
              let tempUser = this.state.users;
              // tempUser[index] = null;
              delete tempUser[index].request;
              if(this.props.refresh != null){
                  this.props.refresh();
              }
              this.setState({
                  users: tempUser
              })
            }, error => {
              switchLoader();
            });
        }
      }
    
      cancelRequest = (id,index, item) => {
        switchLoader(true, 'Cancelling...! ');
        cancelRequest({id}, true)
          .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            // tempUser[index] = null;
            delete tempUser[index].request;
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
          });
      }

    render() {

        return (
            <div className="widget empty-inner-container-with-border myfriends friendsWidget">
                <div className="container">
                    <div className="row">
                        <ul className="list-group w-100">
                        { this.state.search ?
                                    <li className="list-group-item d-flex justify-content-between align-items-center widgetTitle">
                                                <form className="w-100">
                                                    <div className="input-group search-group">
                                                    <input type="text" className="form-control" autoFocus placeholder="Search" id="friendSearch" onChange={this.search} />
                                                    <div className="input-group-append">
                                                    <button className="btn btn-secondary search-btn" type="button" onClick={() => this.setState({search:false})}>
                                                    <i className="fa fa-times"></i>
                                                    </button>
                                                    </div>
                                                    </div>
                                                </form>                                    
                                    </li>
            :
            <li className="widgetTitle">
            <i className="fa fa-users"></i> <span>Make New Friends</span>
            </li>
            }       

                        </ul>
                        {
                            this.state.users.length === 0 && this.state.toggleSkeleton && <p className="noDataFound">No Data Found</p> 
                        }
                        {this.state.users.map((user, i) => (
                            <ul className="list-group w-100" key={i}>
                                <li className="list-group-item">
                                    <div className="media">
                                        <div className="media-left">
                                            <A href={"/u/"+((user.request && user.request == 'pending') ? user.uid : user._id)}>
                                                <img className="media-object circle widgetAvatar" src={profilePic(user.avatar)}
                                                    alt="..." />
                                            </A>
                                        </div>
                                        <div className="media-body">
                                        <A href={"/u/"+((user.request && user.request == 'pending') ? user.uid : user._id)}><p className="media-heading">{user.name}</p></A>
                                            {/* <p className="media-subheading">{user.job == null ? "Eclipton User" : user.job}</p> */}
                                        </div>
                                        <div className="media-right">
                                            {
                                                user.request == undefined && <Button variant="primary-outline round small"   onClick={() => this.addFriend(user._id, i)}><i className="fa fa-plus" ></i></Button>
                                            }
                                            {
                                                user.request == 'pending' && user.type == "sent" &&  <Button variant="dark"  layout="dropdown" dropdownOptions={["Cancel"]} onClick={(n) => {console.log(user); this.cancelRequest(user.uid, i, n)}}>Pending</Button>
                                            }
                                            {
                                                user.request == 'pending' && user.type == "received" && <Button variant="dark" layout="dropdown" dropdownOptions={["Accept", "Reject"]}  onClick={(n) => this.accept(user, i, n)}>Respond</Button>
                                            }
                                            
                                        </div>

                                    </div>
                                </li>
                            </ul>
                        ))}

                        {(!this.state.toggleSkeleton) &&
                            <ul className="list-group w-100">
                                <li className="list-group-item">
                                    {
                                    Array(3)
                                        .fill()
                                        .map((item, index) => (
                                            <ContentLoader
                                                speed={2}
                                                height={40}
                                                viewBox="0 0 200 40"
                                                backgroundColor="#f3f3f3"
                                                foregroundColor="#ecebeb"
                                                {...this.props}
                                                key={index}
                                            >
                                                <rect x="48" y="8" rx="3" ry="3" width="88" height="6" />
                                                <rect x="48" y="26" rx="3" ry="3" width="52" height="6" />
                                                <circle cx="20" cy="20" r="20" />
                                            </ContentLoader>
                                        ))}
                                </li>
                            </ul>
                        }
                        {
                            this.state.seemore && 
                                    <A href={"/all/suggested"} className="seemore-text">See More
                                    </A>
                        }
                        

                    </div>
                </div>
            </div>
        )
    }
}

export default RecommendedFriends;