import config from "../config/index";
import {
    makeAxiosGetRequest
} from "./http-service";


export const getMonthlyBalance = (params) => {
    return new Promise((resolve, reject) => {
        makeAxiosGetRequest(config.baseUrl + "get-monthly-balance", true, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                reject(e.response);
            });
    });
};

export const getAllTransactions = (params) => {
    return new Promise((resolve, reject) => {
        makeAxiosGetRequest(config.baseUrl + "get-transactions", true, params)
        .then(res => {
            resolve(res.data);
        })
        .catch(e => {
            reject(e.response);
        });
    });
};